<template>
  <div id="app">
    <div v-if="!divConfig">
      <h1>Calculo do valor da rota</h1>
        <form @submit.prevent="calcular">
          <div class="form-group">
            <input type="text" id="gasPrice" v-model="distancia" required placeholder="Insira a distÃ¢ncia"/>
          </div>
          <div class="form-group">
            <input type="text" id="ethanolPrice" v-model="tempo" required placeholder="Insira o tempo de viagem"/>
          </div>
          <div class="form-group">
            <input type="text" id="pedagio" v-model="pedagio" placeholder="Insira o valor do pedÃ¡gio se houver"  />
          </div>
          <button type="submit" class="btn-primary">Calcular</button>
          <button type="button" @click="divConfig = true" class="btn-light">ConfiguraÃ§Ãµes</button>
        </form>
        <div v-if="result" class="result">
          <p>{{ result }}</p>
        </div>
    </div>   
    <div v-if="divConfig">
      <h1>ConfiguraÃ§Ãµes</h1>
      <form class="form-container">
        <div class="form-group">
          <input type="text" v-model="valKM" required placeholder="Valor a cobrar por KM"/>
        </div>
        <div class="form-group">
          <input type="text" v-model="valTempo" required placeholder="Valor a cobrar por minuto"/>
        </div>
        <div class="form-group">
          <input type="text" v-model="taxaMinima" required placeholder="Taxa mÃ­nima"/>
        </div>
        <div class="form-group">
          <div class="input-container">
            <select v-model="tipoCombustivel" required>
              <option value="etanol">Etanol</option>
              <option value="gasolina">Gasolina</option>
            </select>
          </div>
          <div class="input-contents">
            <input style="margin-left: 10px;" type="text" v-model="valCombustivel" required placeholder="Valor do combustÃ­vel"/>
          </div>
        </div>   
        <div class="form-group">
          <input type="text" v-model="kmPorLitro" required placeholder="Consumo KM por Litro"/>
        </div>    
        <button type="button" @click="salvar" class="btn-primary">Salvar</button>
      </form>
    </div>
    
    <div v-if="valorCobrar != 0" class="result-container">
      <h1>Resultado</h1>
      <div class="result-item">
        <span class="result-label">Desp. CombustÃ­vel:</span>
        <span class="result-value">{{ this.despesaCombustivel }}</span>
      </div>
      <div class="result-item">
        <span class="result-label">Desp. Ãleo/Filtro:</span>
        <span class="result-value">{{ this.despesaOleoFiltros }}</span>
      </div>
      <div class="result-item">
        <span class="result-label">Desp. Pneus:</span>
        <span class="result-value">{{ this.despesaPneus }}</span>
      </div>
      <div class="result-item">
        <span class="result-label">Desp. Lavagem:</span>
        <span class="result-value">{{ this.despesaLavagem }}</span>
      </div>
      <div class="result-item total">
        <span class="result-label">Desp. Total:</span>
        <span class="result-value">{{ this.despesaTotal }}</span>
      </div>
      <div class="result-item total">
        <span class="result-label">Valor Ã  cobrar:</span>
        <span class="result-value">{{ this.valorCobrar }}</span>
      </div>
      <button type="button" @click="valorCobrar = 0" class="btn-light">Calcular Novamente</button>
    </div>

  </div>
</template>

<script>
export default {
  name: 'CalculadoraComponent',

  data() {
    return {
      distancia: '',
      tempo: '',
      pedagio: '',
      result: '',
      divConfig: true,
      tipoCombustivel: "gasolina",
      valKM: '',
      valTempo: '',
      taxaMinima: '',
      valCombustivel: '',
      kmPorLitro: '',

      keyConfig: 'config',
      
      despesaCombustivel: 0,
      despesaOleoFiltros: 0,
      despesaPneus: 0,
      despesaLavagem: 0,
      despesaTotal: 0,

      valorCobrar: 0
    };
  },
  mounted() {
    const jsonString = localStorage.getItem(this.keyConfig);
  
    if (jsonString !== null) {
      const jsonObject = JSON.parse(jsonString);

      this.valKM = jsonObject.valKM;
      this.valTempo = jsonObject.valTempo;
      this.taxaMinima = jsonObject.taxaMinima;
      this.tipoCombustivel = jsonObject.tipoCombustivel;
      this.valCombustivel =  jsonObject.valCombustivel;
      this.kmPorLitro =  jsonObject.kmPorLitro;

      this.divConfig = false;

    } else {
      this.divConfig = true;
    }    
  },
  methods: {
    calcular() {
      // Valores fixos de despesas
      const valorTrocaOleoFiltro = 180; // A troca de Ã³leo geralmente Ã© em 10 mil KMs
      const valorCustoPneu = 1400; // O valor do custo do pneu aro 15 Ã© em mÃ©dia R$1500
      const valorLavagem = 100; // A cada 1000km Ã© gasto em torno de R$200 com lavagem e limpeza do veÃ­culo
      
      // VerificaÃ§Ã£o e conversÃ£o dos valores de entrada
      const distancia = parseFloat(this.distancia) || 0;
      const kmPorLitro = parseFloat(this.kmPorLitro) || 1; // Evita divisÃ£o por zero
      const valCombustivel = parseFloat(this.valCombustivel) || 0;
      const tempo = parseFloat(this.tempo) || 0;
      const valTempo = parseFloat(this.valTempo) || 0;
      
      // CÃ¡lculo das despesas
      this.despesaCombustivel =  (distancia / kmPorLitro) * valCombustivel;
      this.despesaOleoFiltros = (valorTrocaOleoFiltro / 10000) * distancia;
      this.despesaPneus = (valorCustoPneu / 40000) * distancia;
      this.despesaLavagem = (valorLavagem / 1000) * distancia;
      
      // Despesa por tempo de viagem
      let despesaPorTempoViagem = valTempo * tempo;
      
      // Despesa total
      this.despesaTotal = (
        this.despesaCombustivel +  // Despesa com combustÃ­vel
        this.despesaOleoFiltros +  // Despesa com troca de Ã³leo e filtros
        this.despesaPneus +        // Despesa com pneus
        this.despesaLavagem        // Despesa com lavagem              
      );

      this.valorCobrar = this.taxaMinima + (distancia * this.valKM) + despesaPorTempoViagem;

      // Logs para depuraÃ§Ã£o
      console.log("Despesa CombustÃ­vel:", this.despesaCombustivel);
      console.log("Despesa Ãleo e Filtros:", this.despesaOleoFiltros);
      console.log("Despesa Pneus:", this.despesaPneus);
      console.log("Despesa Lavagem:", this.despesaLavagem);
      console.log("Despesa por Tempo de Viagem:", despesaPorTempoViagem);
      console.log("Despesa Total:", this.despesaTotal);
    },
    salvar() {
      let obj = {
        valKM: parseFloat(this.valKM),
        valTempo: parseFloat(this.valTempo),
        taxaMinima: parseFloat(this.taxaMinima),
        tipoCombustivel: this.tipoCombustivel,
        valCombustivel: parseFloat(this.valCombustivel),
        kmPorLitro: this.kmPorLitro
      };
      localStorage.setItem("config", JSON.stringify(obj));
      this.divConfig = false;
    }

  }
}
</script>

